<template>
  <div class="home">
    <label for="csv">Archivo</label>
    <vue-csv-import v-model="csv" :fields="fields">
      <vue-csv-errors></vue-csv-errors>
      <vue-csv-input id="csv"></vue-csv-input>
      <vue-csv-map>
        <p></p>
      </vue-csv-map>
    </vue-csv-import>
    <select v-model="sortBy">
      <option v-for="field in fields" :key="field">{{ field }}</option>
    </select>
    <select v-model="sortOrder">
      <option value="asc">Ascendiente</option>
      <option value="desc">Descendiente</option>
    </select>
    <input type="text" v-model="word" />
    <button @click="addFilterWord">AÃ±adir</button>
    <div>
      <p v-for="(word, i) in filterWords" :key="i">
        {{ word }}
        <button @click="removeFilterWord(i)">Eliminar</button>
      </p>
    </div>
    <table>
      <th v-for="field in fields" :key="field">
        {{ field }}
      </th>
      <tbody>
        <tr v-for="field in excludedSorted ?? []" v-bind:key="field">
          <td v-for="column in fields" :key="column">
            {{ field[column] }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import {
  VueCsvImport,
  VueCsvInput,
  VueCsvMap,
  VueCsvErrors,
} from "vue-csv-import";

import naturalSort from "natural-sort";

export default {
  name: "FilterView",
  data() {
    return {
      csv: null,
      filterWords: [],
      word: "",
      sortBy: "Group ID",
      sortOrder: "desc",
      fields: [
        "Name",
        "Group ID",
        "Members",
        "Privacy",
        "Post Status",
        "Admins",
      ],
    };
  },
  methods: {
    addFilterWord() {
      this.filterWords.push(this.word.toLowerCase());
      this.word = "";
    },
    removeFilterWord(i) {
      this.filterWords.splice(i, 1);
    },
  },
  computed: {
    withoutHeaderCsv() {
      return this.csv?.slice(1) ?? [];
    },
    sorted() {
      return this.withoutHeaderCsv
        ?.map((field) => field)
        ?.sort((a, b) =>
          naturalSort({ direction: this.sortOrder })(
            a[this.sortBy],
            b[this.sortBy]
          )
        );
    },
    excludedSorted() {
      return this.sorted?.filter((filter) =>
        !filter.Name.toLowerCase().includes("venta") &&
        !filter.Name.toLowerCase().includes("compra") &&
        this.filterWords.length == 0
          ? true
          : this.filterWords.some((word) =>
              Boolean(filter.Name.toLowerCase().match(new RegExp(word)))
            )
      );
    },
  },
  components: {
    VueCsvImport,
    VueCsvInput,
    VueCsvMap,
    VueCsvErrors,
  },
};
</script>

